import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'CyberMint',
  description:
    ' Earn CyberMints through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by CyberMint), NFTs, and more, on a platform you can trust.',
  image: 'https://CyberMint.finance/images/hero.png',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  let basePath
  if (path.startsWith('/swap')) {
    basePath = '/swap'
  } else if (path.startsWith('/add')) {
    basePath = '/add'
  } else if (path.startsWith('/remove')) {
    basePath = '/remove'
  } else if (path.startsWith('/teams')) {
    basePath = '/teams'
  } else if (path.startsWith('/voting/proposal') && path !== '/voting/proposal/create') {
    basePath = '/voting/proposal'
  } else {
    basePath = path
  }

  switch (basePath) {
    case '/':
      return {
        title: `${t('Home')} | ${t('CyberMint')}`,
      }
    case '/swap':
      return {
        title: `${t('BSC Exchange')} | ${t('CyberMint')}`,
      }
    case '/add':
      return {
        title: `${t('Add Liquidity')} | ${t('CyberMint')}`,
      }
    case '/remove':
      return {
        title: `${t('Remove Liquidity')} | ${t('CyberMint')}`,
      }
    case '/liquidity':
      return {
        title: `${t('Liquidity')} | ${t('CyberMint')}`,
      }
    case '/find':
      return {
        title: `${t('Import Pool')} | ${t('CyberMint')}`,
      }
    case '/competition':
      return {
        title: `${t('Trading Battle')} | ${t('CyberMint')}`,
      }
    case '/prediction':
      return {
        title: `${t('Prediction')} | ${t('CyberMint')}`,
      }
    case '/prediction/leaderboard':
      return {
        title: `${t('Leaderboard')} | ${t('CyberMint')}`,
      }
    case '/farms':
      return {
        title: `${t('BSC Farms')} | ${t('CyberMint')}`,
      }
    case '/farms/auction':
      return {
        title: `${t('Farm Auctions')} | ${t('CyberMint')}`,
      }
    case '/pools':
      return {
        title: `${t('BSC Pools')} | ${t('CyberMint')}`,
      }
    case '/lottery':
      return {
        title: `${t('Lottery')} | ${t('CyberMint')}`,
      }
    case '/collectibles':
      return {
        title: `${t('Collectibles')} | ${t('CyberMint')}`,
      }
    case '/ifo':
      return {
        title: `${t('Initial Farm Offering')} | ${t('CyberMint')}`,
      }
    case '/teams':
      return {
        title: `${t('Leaderboard')} | ${t('CyberMint')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('CyberMint')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Task Center')} | ${t('CyberMint')}`,
      }
    case '/voting':
      return {
        title: `${t('Voting')} | ${t('CyberMint')}`,
      }
    case '/voting/proposal':
      return {
        title: `${t('Proposals')} | ${t('CyberMint')}`,
      }
    case '/voting/proposal/create':
      return {
        title: `${t('Make a Proposal')} | ${t('CyberMint')}`,
      }
    case '/info':
      return {
        title: `${t('Overview')} | ${t('CyberMint Info & Analytics')}`,
        description: 'View statistics for CyberMint exchanges.',
      }
    case '/info/pools':
      return {
        title: `${t('Pools')} | ${t('CyberMint Info & Analytics')}`,
        description: 'View statistics for CyberMint exchanges.',
      }
    case '/info/tokens':
      return {
        title: `${t('Pools')} | ${t('CyberMint Info & Analytics')}`,
        description: 'View statistics for CyberMint exchanges.',
      }
    default:
      return null
  }
}
